import i18n from "i18next";
import LanguageDetector from "i18next-browser-languagedetector";
import { initReactI18next } from "react-i18next";

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    resources: {
      en: {
        translations: {
          "ToDo": "List of todos",
          "Loading...": "Loading...",
          "List of todos is empty ": "List of todos is empty  ",
        }
      },
      de: {
        translations: {
          "ToDo": "Liste der Aufgaben",
          "Loading...": "Laden...",
          "List of todos is empty ": "Liste der Aufgaben ist leer  ",
        }
      },
      ru: {
        translations: {
          "ToDo": " ",
          "Loading...": "...",
          "List of todos is empty ": "   ",
        }
      },
    },
    fallbackLng: "en",
    debug: true,

    ns: ["translations"],
    defaultNS: "translations",

    keySeparator: false,

    interpolation: {
      escapeValue: false
    }
  });

export default i18n;